<template>
<!-- eslint-disable -->
  <b-nav-item-dropdown
    class="dropdown-notification mr-25"
    menu-class="dropdown-menu-media"
    right
  >
    <template #button-content>
      <feather-icon
        :badge="notifications.length"
        badge-classes="bg-danger"
        class="text-body"
        icon="BellIcon"
        size="21"
      />
    </template>

    <!-- Header -->
    <li class="dropdown-menu-header">
      <div class="dropdown-header d-flex">
        <h4 class="notification-title mb-0 mr-auto">
          Notificaciones
        </h4>
        <b-badge
          pill
          variant="light-primary"
        >
          {{ notifications.length }}
        </b-badge>
      </div>
    </li>

    <!-- Notifications -->
    <vue-perfect-scrollbar
      :settings="perfectScrollbarSettings"
      class="scrollable-container media-list scroll-area"
      tagname="li"
    >
      <!-- Account Notification -->
      <div
        v-for="notification in notifications"
        :key="notification.id"
      >
        <a href="https://www.google.com.co" target="_blank">
          <b-media>
          <template #aside>
            <!-- <b-avatar
              size="32"
              :src="require('@/assets/images/avatars/6-small.png')"
              :text="notification.avatar"
              :variant="notification.type"
            /> -->
            <b-avatar
              size="32"
              variant="light-success"
            >
              <feather-icon icon="InfoIcon" />
            </b-avatar>
          </template>
          <h6>Compra: #{{ notification.purchase_id }}</h6>
          <p class="media-heading">
            <span class="font-weight-bolder">
              {{ notification.message }}
            </span>
          </p>
          <small class="notification-text">Enviado por: {{ notification.usuario_envia }} / {{ notification.created_at }}</small>
        </b-media>
        </a>
      </div>

      <!-- System Notification Toggler -->
      <div class="media d-flex align-items-center">
        <h6 class="font-weight-bolder mr-auto mb-0">
          System Notifications
        </h6>
        <b-form-checkbox
          :checked="true"
          switch
        />
      </div>

      <!-- System Notifications -->
      <!-- <b-link
        v-for="notification in systemNotifications"
        :key="notification.subtitle"
      >
        <b-media>
          <template #aside>
            <b-avatar
              size="32"
              :variant="notification.type"
            >
              <feather-icon :icon="notification.icon" />
            </b-avatar>
          </template>
          <p class="media-heading">
            <span class="font-weight-bolder">
              {{ notification.title }}
            </span>
          </p>
          <small class="notification-text">{{ notification.subtitle }}</small>
        </b-media>
      </b-link> -->
    </vue-perfect-scrollbar>

    <!-- Cart Footer -->
    <li class="dropdown-menu-footer"><b-button
      v-ripple.400="'rgba(255, 255, 255, 0.15)'"
      variant="primary"
      block
    >Read all notifications</b-button>
    </li>
  </b-nav-item-dropdown>
</template>

<script>
/* eslint-disable */
// Servicio para obtener las notificaciones
import * as notificationService from '@/services/notifications/notifications'
import {
  BNavItemDropdown, BBadge, BMedia, BLink, BAvatar, BButton, BFormCheckbox,
} from 'bootstrap-vue'
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import Ripple from 'vue-ripple-directive'

// Moment - Fechas
import moment from 'moment'
import 'moment/locale/es'

// Notificaciones
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
  components: {
    BNavItemDropdown,
    BBadge,
    BMedia,
    BLink,
    BAvatar,
    VuePerfectScrollbar,
    BButton,
    BFormCheckbox,
    ToastificationContent
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      notifications: [],
      perfectScrollbarSettings : {
        maxScrollbarLength: 60,
        wheelPropagation: false,
      }
    }
  },

  created() {
    this.getNotifications()
  },

  mounted() {
    //Consulta las notificationes cada 5 minutos
    setInterval(() => {
      this.getNotifications()
    }, 300000)
  },
  methods: {  

    async getNotifications() {
      try {        
        const response = await notificationService.getNotifications()

        if(response.status === 200) {
          this.notifications = response.data.data
          this.notifications.forEach(notification => {
            notification.created_at = moment(notification.created_at).format('LLLL')
          })        
        }
      }catch (error) {
        this.showToast('Warning', 'AlertCircleIcon', 'No se pudieron obtener los datos', 'warning')
      }
    },

    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },
               
  }
}
    
</script>

<style>

</style>
